









import { Vue, Component, Prop, Mixins } from "vue-property-decorator";

@Component({})
export default class SettingsSection extends Mixins() {
  @Prop({
    type: String,
    default: "Section title not set"
  })
  title!: string;
  @Prop({
    type: String,
    default: ""
  })
  sectionClass!: string;
}
