




































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Inject,
  Watch
} from "vue-property-decorator";

@Component({})
export default class SettingsDialog extends Mixins() {
  @Prop({
    type: Boolean,
    default: false
  })
  showSave!: boolean;

  @Prop({
    type: [String, Number, Boolean],
    required: true
  })
  value!: string | number | boolean;

  @Prop({
    type: [String, Number],
    default: ""
  })
  input!: string | number;

  @Prop({
    type: Boolean,
    default: false
  })
  scrollable!: boolean;

  @Prop({
    type: String,
    default: ""
  })
  title!: string;

  @Prop({
    type: String,
    default: "default",
    validator: (val: string) =>
      ["default", "textField", "numberField"].indexOf(val) !== -1
  })
  preset!: "default" | "textField" | "numberField";

  private currentInputValue: string | number = "";

  @Watch("input", { immediate: true })
  onInputChange() {
    this.currentInputValue = this.input;
  }

  @Watch("vModel")
  onModelChange() {
    // TODO: emit cancel when user clicks outside
    this.currentInputValue = this.input;
  }

  get isWide() {
    return this.preset !== "default";
  }

  get vModel() {
    return this.value;
  }
  set vModel(val) {
    this.$emit("input", val);
  }
}
