











































































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Inject,
  Watch
} from "vue-property-decorator";
import SettingsSection from "./SettingsSection.vue";
import SettingsItem from "./SettingsItem.vue";
import SettingsDialogList from "./SettingsDialogList.vue";
import SettingsDialog from "./SettingsDialog.vue";
import colors from "vuetify/es5/util/colors";
import { settingsModule } from "@/store/modules";
import { Language } from "@/shared";

@Component({
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog
  }
})
export default class TheUserSettings extends Mixins() {
  usernameDialog = false;
  firstNameDialog = false;
  emailDialog = false;
  lastNameDialog = false;
  passwordDialog = false;

  get username() {
    return settingsModule.username;
  }
  get firstName() {
    return settingsModule.firstName;
  }
  get email() {
    return settingsModule.email;
  }
  get lastName() {
    return settingsModule.lastName;
  }

  setUsername(name: string) {
    settingsModule.setUsername(name);
  }
  setFirstName(name: string) {
    settingsModule.setFirstName(name);
  }
  setEmail(email: string) {
    settingsModule.setEmail(email);
  }
  setLastName(name: string) {
    settingsModule.setLastName(name);
  }

  updatePassword(password: string) {
    settingsModule.updatePassword(password);
  }
}
