











import {
  Vue,
  Component,
  Prop,
  Mixins,
  Inject,
  Watch
} from "vue-property-decorator";
import SettingsSection from "./SettingsSection.vue";
import SettingsItem from "./SettingsItem.vue";
import SettingsDialog from "./SettingsDialog.vue";
import { settingsModule } from "@/store/modules";
import { IApiMessage, MessageType, IServerResponse } from "@/shared";

@Component({
  components: {
    SettingsItem,
    SettingsDialog,
    SettingsSection
  }
})
export default class TheUpdateSection extends Mixins() {
  lastResponse: IApiMessage = {
    type: MessageType.success,
    name: "setting.update.update-available",
    args: {}
  };
  get updateAvailable() {
    return settingsModule.updateAvailable;
  }
  async updateNow() {
    let response: IServerResponse<{}> | undefined;
    try {
      response = await settingsModule.updateService();
    } catch (err) {
      response = err && err.response && err.response.data;
    }
    const firstMessage = response && response.messages && response.messages[0];
    if (firstMessage) {
      this.lastResponse = firstMessage;
    }
  }
  get sectionClass() {
    return this.lastResponse.type;
  }
  get sectionTitle() {
    return this.$t(this.lastResponse.name, this.lastResponse.args);
  }
}
