
















































































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Inject,
  Watch
} from "vue-property-decorator";
import SettingsSection from "./SettingsSection.vue";
import SettingsItem from "./SettingsItem.vue";
import SettingsDialogList from "./SettingsDialogList.vue";
import SettingsDialog from "./SettingsDialog.vue";
import RecipientInput from "./RecipientInput.vue";
import { settingsModule } from "@/store/modules";
import { ReportLanguage, IApiMessage } from "@/shared";

@Component({
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog,
    RecipientInput
  }
})
export default class TheNotificationSettings extends Mixins() {
  smtpDialog = false;
  smtpPortDialog = false;
  reportLanguageDialog = false;

  currentSmtpHostname = "";
  currentSmtpPort = 0;
  currentSmtpFrom = "";
  currentSmtpPassword = "";
  currentSmtpUsername = "";
  currentSmtpRecipients: string[] = [];

  smtpResponse: IApiMessage | null = null;

  async testSmtpConnection() {
    let response;
    try {
      response = await settingsModule.testSmtpSettings({
        subject: this.$t("setting.smtp.test-mail-subject").toString(),
        text: this.$t("setting.smtp.test-mail-body").toString()
      });
    } catch (err) {
      response = err && err.response && err.response.data;
    }
    if (response) {
      this.smtpResponse = response.messages[0];
    }
  }

  get errorDescription() {
    return (
      this.smtpResponse &&
      this.$t(this.smtpResponse.name, this.smtpResponse.args)
    );
  }

  get sendEmails() {
    return settingsModule.sendEmails;
  }
  get reportLanguage() {
    return settingsModule.reportLanguage;
  }
  get smtpHostname() {
    return settingsModule.smtpHostname;
  }
  get smtpPort() {
    return settingsModule.smtpPort;
  }
  get smtpFrom() {
    return settingsModule.smtpFrom;
  }
  get smtpPassword() {
    return settingsModule.smtpPassword;
  }
  get smtpUsername() {
    return settingsModule.smtpUsername;
  }
  get smtpRecipients() {
    return settingsModule.smtpRecipients;
  }

  get availableReportLanguages() {
    return [
      {
        value: ReportLanguage.gui,
        avatar: "G",
        title: this.$t("language.same-as-gui"),
        active: this.reportLanguage === ReportLanguage.gui
      },
      {
        value: ReportLanguage.de,
        avatar: "DE",
        title: this.$t("language.german"),
        active: this.reportLanguage === ReportLanguage.de
      },
      {
        value: ReportLanguage.en,
        avatar: "EN",
        title: this.$t("language.english"),
        active: this.reportLanguage === ReportLanguage.en
      }
    ];
  }
  setSendEmails(val: boolean) {
    settingsModule.setSendEmails(val);
  }
  setReportLanguage(val: ReportLanguage) {
    settingsModule.setReportLanguage(val);
  }
  setSmtpHostname(val: string) {
    settingsModule.setSmtpHostname(val);
  }
  setSmtpPort(val: number) {
    settingsModule.setSmtpPort(val);
  }
  setSmtpFrom(val: string) {
    settingsModule.setSmtpFrom(val);
  }
  setSmtpPassword(val: string) {
    settingsModule.setSmtpPassword(val);
  }
  setSmtpUsername(val: string) {
    settingsModule.setSmtpUsername(val);
  }
  setSmtpRecipients(val: string[]) {
    settingsModule.setSmtpRecipients(val);
  }

  saveSmtpSettings() {
    this.setSmtpHostname(this.currentSmtpHostname);
    this.setSmtpPort(Number(this.currentSmtpPort));
    this.setSmtpFrom(this.currentSmtpFrom);
    this.setSmtpPassword(this.currentSmtpPassword);
    this.setSmtpUsername(this.currentSmtpUsername);
    this.setSmtpRecipients(this.currentSmtpRecipients); // TODO
  }

  resetSmtpSettings() {
    this.currentSmtpHostname = this.smtpHostname;
    this.currentSmtpPort = this.smtpPort;
    this.currentSmtpFrom = this.smtpFrom;
    this.currentSmtpPassword = this.smtpPassword;
    this.currentSmtpUsername = this.smtpUsername;
    this.currentSmtpRecipients = this.smtpRecipients;
  }

  @Watch("smtpHostname", { immediate: true })
  @Watch("smtpPort", { immediate: true })
  @Watch("smtpFrom", { immediate: true })
  @Watch("smtpPassword", { immediate: true })
  @Watch("smtpUsername", { immediate: true })
  @Watch("smtpRecipients", { immediate: true })
  onSettingsChange() {
    this.currentSmtpHostname = this.smtpHostname;
    this.currentSmtpPort = this.smtpPort;
    this.currentSmtpFrom = this.smtpFrom;
    this.currentSmtpPassword = this.smtpPassword;
    this.currentSmtpUsername = this.smtpUsername;
    this.currentSmtpRecipients = this.smtpRecipients;
  }
}
