



















































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Inject,
  Watch
} from "vue-property-decorator";
import SettingsSection from "./SettingsSection.vue";
import SettingsItem from "./SettingsItem.vue";
import SettingsDialogList from "./SettingsDialogList.vue";
import SettingsDialog from "./SettingsDialog.vue";
import { settingsModule } from "@/store/modules";
import { UpdateCheckInterval } from "@/shared";

@Component({
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog
  }
})
export default class TheGeneralSettings extends Mixins() {
  updateCheckDialog = false;
  portDialog = false;

  get automaticUpdates() {
    return settingsModule.automaticUpdates;
  }

  get updateCheckInterval() {
    return settingsModule.updateCheckInterval;
  }

  get enableRegister() {
    return settingsModule.enableRegister;
  }

  get port() {
    return settingsModule.port;
  }

  get availableUpdateIntervals() {
    return [
      {
        value: UpdateCheckInterval.hourly,
        avatar: String(this.$t("setting.update-check-interval.hourly"))
          .substr(0, 1)
          .toUpperCase(),
        title: this.$t("setting.update-check-interval.hourly"),
        active: this.updateCheckInterval === UpdateCheckInterval.hourly
      },
      {
        value: UpdateCheckInterval.daily,
        avatar: String(this.$t("setting.update-check-interval.daily"))
          .substr(0, 1)
          .toUpperCase(),
        title: this.$t("setting.update-check-interval.daily"),
        active: this.updateCheckInterval === UpdateCheckInterval.daily
      },
      {
        value: UpdateCheckInterval.weekly,
        avatar: String(this.$t("setting.update-check-interval.weekly"))
          .substr(0, 1)
          .toUpperCase(),
        title: this.$t("setting.update-check-interval.weekly"),
        active: this.updateCheckInterval === UpdateCheckInterval.weekly
      }
    ];
  }

  setAutomaticUpdates(enable: boolean) {
    settingsModule.setAutomaticUpdates(enable);
  }
  setUpdateCheckInterval(interval: UpdateCheckInterval) {
    settingsModule.setUpdateCheckInterval(interval);
  }
  setEnableRegister(enable: boolean) {
    settingsModule.setEnableRegister(enable);
  }
  setPort(port: number) {
    settingsModule.setPort(port);
  }
}
