<template>
  <v-combobox
    v-model="vModel"
    :items="items"
    :search-input.sync="search"
    hide-selected
    :label="label"
    multiple
    small-chips
    append-icon=" "
    box
  >
    <template slot="no-data"></template>
  </v-combobox>
</template>

<script>
// TODO: email validating, props
export default {
  props: {
    label: {
      type: String,
      default: undefined
    }
  },
  data: () => ({
    items: [],
    search: null
  }),
  watch: {
    model(val) {
      if (val.length > 5) {
        this.$nextTick(() => this.model.pop());
      }
    }
  },
  computed: {
    vModel: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
      }
    }
  }
};
</script>