

























































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Inject,
  Watch
} from "vue-property-decorator";
import SettingsSection from "./SettingsSection.vue";
import SettingsItem from "./SettingsItem.vue";
import SettingsDialogList from "./SettingsDialogList.vue";
import SettingsDialog from "./SettingsDialog.vue";
import ThemeItem from "./ThemeItem.vue";
import colors from "vuetify/es5/util/colors";
import { settingsModule } from "@/store/modules";
import { Language } from "@/shared";

@Component({
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog,
    ThemeItem
  }
})
export default class TheGuiSettings extends Mixins() {
  themeDialog = false;
  languageDialog = false;
  get availableThemes() {
    return [
      {
        primary: "#456CB2",
        accent: "#424242",
        secondary: "#82B1FF"
      },
      {
        primary: "#456CB2",
        accent: colors.green.base,
        secondary: colors.blueGrey.base
      },
      {
        primary: "#456CB2",
        accent: colors.red.base,
        secondary: colors.blueGrey.base
      },
      {
        primary: colors.deepOrange.base,
        accent: colors.orange.darken1,
        secondary: colors.blueGrey.base
      },
      {
        primary: colors.pink.base,
        accent: colors.lightBlue.base,
        secondary: colors.blueGrey.base
      },
      {
        primary: colors.lightGreen.base,
        accent: colors.deepOrange.base,
        secondary: colors.blueGrey.base
      },
      {
        primary: colors.blueGrey.base,
        accent: "#456CB2",
        secondary: colors.blueGrey.base
      },
      {
        primary: colors.yellow.darken3,
        accent: colors.pink.lighten3,
        secondary: colors.blueGrey.base
      },
      {
        primary: colors.teal.darken3,
        accent: colors.red.darken2,
        secondary: colors.blueGrey.base
      },
      {
        primary: colors.shades.black,
        accent: colors.grey.base,
        secondary: colors.blueGrey.base
      },
      {
        primary: colors.amber.base,
        accent: colors.brown.base,
        secondary: colors.blueGrey.base
      }
    ].map(t => ({
      ...t,
      active:
        this.theme.primary === t.primary &&
        this.theme.secondary === t.secondary &&
        this.theme.accent === t.accent
    }));
  }

  get theme() {
    return settingsModule.theme;
  }

  get showSnackbar() {
    return settingsModule.showSnackbar;
  }

  get currentLanguage() {
    return settingsModule.language;
  }

  get availableLanguages() {
    return [
      {
        value: Language.de,
        avatar: "DE",
        title: this.$t("language.german"),
        active: this.currentLanguage === Language.de
      },
      {
        value: Language.en,
        avatar: "EN",
        title: this.$t("language.english"),
        active: this.currentLanguage === Language.en
      }
    ];
  }

  setTheme(theme: { primary: string; accent: string; secondary: string }) {
    settingsModule.setTheme(theme);
  }
  setDark(dark: boolean) {
    settingsModule.setDark(dark);
  }
  setLanguage(locale: Language) {
    settingsModule.setLanguage(locale);
  }
  setShowSnackbar(show: boolean) {
    settingsModule.setShowSnackbar(show);
  }
}
