
















































import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { settingsModule } from "@/store/modules";

import SettingsSection from "./SettingsSection.vue";
import SettingsItem from "./SettingsItem.vue";
import SettingsDialogList from "./SettingsDialogList.vue";
import SettingsDialog from "./SettingsDialog.vue";
import { EmailNotification } from "@/shared";

@Component({
  components: {
    SettingsSection,
    SettingsItem,
    SettingsDialogList,
    SettingsDialog
  }
})
export default class TheBackupSettings extends Mixins() {
  defaultEmailNotificationDialog = false;
  defaultMaxBackupsPerRepoDialog = false;
  defaultUnlockTimeDialog = false;

  get defaultEmailNotification() {
    return settingsModule.defaultEmailNotification;
  }
  get defaultMaxBackupsPerRepo() {
    return settingsModule.defaultMaxBackupsPerRepo;
  }
  get defaultUnlockTime() {
    return settingsModule.defaultUnlockTime;
  }

  get defaultEmailNotifications() {
    return [
      {
        value: EmailNotification.always,
        title: this.$t("setting.default-email-notification-type.always"),
        active: this.defaultEmailNotification === EmailNotification.always
      },
      {
        value: EmailNotification.onerror,
        title: this.$t("setting.default-email-notification-type.on-error"),
        active: this.defaultEmailNotification === EmailNotification.onerror
      },
      {
        value: EmailNotification.never,
        title: this.$t("setting.default-email-notification-type.never"),
        active: this.defaultEmailNotification === EmailNotification.never
      }
    ];
  }

  setDefaultEmailNotification(type: EmailNotification) {
    settingsModule.setDefaultEmailNotification(type);
  }
  setDefaultMaxBackupsPerRepo(max: number) {
    settingsModule.setDefaultMaxBackupsPerRepo(max);
  }
  setDefaultUnlockTime(time: number) {
    settingsModule.setDefaultUnlockTime(time);
  }
}
