








































































import { Vue, Component, Prop, Mixins, Inject } from "vue-property-decorator";

@Component({})
export default class ThemeItem extends Mixins() {
  @Inject("theme")
  theme!: { isDark: boolean };
  @Prop({
    type: String,
    required: true
  })
  primaryColor!: string;
  @Prop({
    type: String,
    required: true
  })
  secondaryColor!: string;
  @Prop({
    type: String,
    required: true
  })
  accentColor!: string;
}
