<template>
  <v-list two-line>
    <v-list-tile
      ripple
      v-for="(item, index) in list"
      :key="index"
      avatar
      @click="$emit('click', item)"
    >
      <v-list-tile-avatar
        v-if="showAvatar"
        :color="getActiveItem(item) ? 'accent' : undefined"
      >{{ getAvatar(item) }}</v-list-tile-avatar>
      <v-list-tile-action v-else>
        <v-icon
          :color="getActiveItem(item) ? 'accent' : undefined"
        >{{getActiveItem(item) ? "mdi-radiobox-marked" : "mdi-radiobox-blank"}}</v-icon>
      </v-list-tile-action>
      <v-list-tile-content>
        <v-list-tile-title>{{ getTitle(item) }}</v-list-tile-title>
      </v-list-tile-content>
    </v-list-tile>
  </v-list>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      default: () => []
    },
    getAvatar: {
      type: Function,
      default: i => (i && i.avatar) || i
    },
    getTitle: {
      type: Function,
      default: i => (i && i.title) || i
    },
    getActiveItem: {
      type: Function,
      default: i => !!(i && i.active)
    },
    showAvatar: {
      type: Boolean,
      default: false
    }
  }
};
</script>
